import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/layout'
Vue.use(Router)

export const constantRoutes = [
  {
    path: '/login',
    name: 'login',
    component: () => import('@/components/Login'),
    meta: { title: '登陆' }
    //   hidden: true
  },

  {
    path: '/404',
    name: '404',
    component: () => import('@/components/404'),
    meta: { title: '404' }
  },
  {
    path: '/',
    redirect: '/home',
    component: Layout,
    children: [
      {
        path: '/home',
        name: 'home',
        component: () => import('@/components/Home'),
        meta: { title: '首页' }
      },
      {
        path: '/tasks',
        name: 'tasks',
        component: () => import('@/components/task/Tasks'),
        meta: { title: '任务列表' }
      },
      {
        path: '/task',
        name: 'task',
        component: () => import('@/components/task/Task'),
        meta: { title: '任务详情' }
      },
      {
        path: '/forms',
        name: 'forms',
        component: () => import('@/components/form/Forms'),
        meta: { title: '表单' }
      }, {
        path: '/upload',
        name: 'upload',
        component: () => import('@/components/form/Upload'),
        meta: { title: '导入' }
      },
      {
        path: '/formEdit',
        name: 'formEdit',
        component: () => import('@/components/form/FormEdit'),
        meta: { title: '表单编辑' }
      },
      {
        path: '/processes',
        name: 'processes',
        component: () => import('@/components/process/Processes'),
        meta: { title: '流程' }
      },
      {
        path: '/processEdit',
        name: 'processEdit',
        component: () => import('@/components/process/processEdit'),
        meta: { title: '流程编辑' }
      },
      {
        path: '/calendar',
        name: 'calendar',
        component: () => import('@/components/calendar/Home'),
        meta: { title: '日历' }
      }
    ]
  }
]

const createRouter = () => new Router({
  // mode: 'history', // require service support
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRoutes
})

const router = createRouter()

// Detail see: https://github.com/vuejs/vue-router/issues/1234#issuecomment-357941465
export function resetRouter() {
  const newRouter = createRouter()
  router.matcher = newRouter.matcher // reset router
}

export default router
