import { resetRouter } from '@/router'
import moment from "moment";
import axios from 'axios';
import utils from "./utils";
import config from "./config";

const getUser = () => {
    var user = sessionStorage.getItem("user");
    if (user) {
        return JSON.parse(user);
    }
    return null;
};

const login = (name, password) => {

    return new Promise((resolve, reject) => {
        sessionStorage.removeItem("bearer");
        sessionStorage.removeItem("user");

        var token = utils.getAuthToken(config.oAuth2Keys[0], config.oAuth2Keys[1]);
        var url = config.oAuth2Url + "token";

        var settings = {
            dataType: "json",
            url: url,
            method: "POST",
           // contentType: "application/json; charset=utf-8",
            headers: {
                "Authorization": token,
                "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
            },
            data: $.param({
                "grant_type": "password",
                "username": name,
                "password": password,
                "scope": "openid email"
            })
        };

        return axios(settings).then(
            response => {
                if (response && response.data && response.data.access_token) {
                    sessionStorage.setItem("bearer", JSON.stringify(response.data));
                }

                var url = config.oAuth2Url + "userinfo";
                var settings = {
                    dataType: "json",
                    url: url,
                    headers: {
                        "Authorization": "Bearer " + response.data.access_token
                    },
                    method: "GET",
                    params: {
                        "schema": "openid"
                    }
                };
                return axios(settings);
            }, (error) => {
                return Promise.reject(error);
            }
        ).then((response) => {
            var user= response.data;
            user.lastLogin = +moment();
            user.id = name;
            // if (user.Roles) {
            //     var roles = user.Roles.split(",").map(function (role) {
            //         return role.replace(/(.*)\//, "");
            //     });
            //     user.Roles = roles.join(",");
            // }
            var token = utils.getAuthToken(name, password);
            sessionStorage.setItem("token", token);
            sessionStorage.setItem("user", JSON.stringify(user));
            resolve(user);
        }, (error) => {
            reject(error);
        });

    });
};

const logout = () => {
    sessionStorage.removeItem("user");
    resetRouter();

};


export default {
    getUser,
    login,
    logout
}
