<script>
import auth from "@/common/auth";
export default {
  name: 'Layout',
    data() {
    return {
      user: null,
      message:{
        title:"",
        type:"",
        message:""
      }
    }
  },
   created: function() {
     this.user= auth.getUser();
     if(!this.user){
       this.logout();
     }
     this.$on("message",message=>{
       this.message=message;
         $("#messageModal").modal("show");
     });
   },
  watch:{
    $route(to,from){
      //console.log(to);
       $(".left-menu").modal("hide");
       
    }
  },
  computed:{
  
  
  },
  methods: {
    logout(){
        auth.logout();
        this.$router.push("/login?redirect="+this.$router.currentRoute.fullPath);
    },
    submit(){
        this.$children[0].$emit("submit");
    }
  }
}
</script>

